import React, { useState } from 'react';
import styles from './index.module.scss';

export default function Logo({ variant, onClick = () => {} }) {
    const [c, setC] = useState(0);
    const egg = (
        <code className={styles.greenText}>
            01000110 01110101 01100011 01101011 00100000 01111001 01101111
            01110101 00101110 00100000 01010011 01110100 01101111 01110000
            00100000 01101101 01100101 01110011 01110011 01101001 01101110
            01100111 00100000 01110111 01101001 01110100 01101000 00100000
            01101101 01111001 00100000 01110111 01100101 01100010 01110011
            01101001 01110100 01100101
        </code>
    );
    const handleClick = () => {
        onClick();
        setC(c + 1);
    };
    return (
        <div className={styles.root} onClick={handleClick}>
            {c < 64 ? (
                <a className={styles[variant]} href="/#">
                    <div alt="noCAPTCHA" />
                </a>
            ) : (
                egg
            )}
        </div>
    );
}
